.add-form {
  background-color: #eee;
  border-radius: 12px;
  justify-content: space-between;
  margin: 0 auto;
  padding: 30px;
}

.add-form,
.input-style {
  align-items: center;
  display: flex;
  grid-gap: 20px;
  gap: 20px;
}

.add-input {
  border: none;
  border-radius: 12px;
  height: 40px;
  padding: 0 12px;
  width: 240px;
}

.form-label {
  font-size: 16px;
  font-weight: 700;
}

.add-button {
  background-color: teal;
  border: none;
  border-radius: 10px;
  color: #fff;
  font-weight: 700;
  height: 40px;
  width: 140px;
}

.square-style {
  border: 4px solid teal;
  border-radius: 12px;
  padding: 12px 24px 24px;
  width: 270px;
}

.button-set {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  margin-top: 24px;
}

.button {
  border: none;
  border-radius: 8px;
  cursor: pointer;
  height: 40px;
  width: 50%;
}

.todo-delete-button {
  background-color: #fff;
  border: 2px solid red;
}

.todo-complete-button {
  background-color: #fff;
  border: 2px solid green;
}

.list-container {
  padding: 0 24px px;
}

h2 {
  display: block;
  font-size: 1.5em;
  -webkit-margin-before: 0.83em;
          margin-block-start: 0.83em;
  -webkit-margin-after: 0.83em;
          margin-block-end: 0.83em;
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;
  font-weight: bold;
}

.app-style {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 12px;
  gap: 12px;
}

.header {
  border: 1px solid rgb(221, 221, 221);
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 20px;
  max-width: 1160px;
  min-width: 800px;
  margin: 0px auto 20px;
}

main {
  display: block;
}

.detailStyles {
  border: 2px solid rgb(238, 238, 238);
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.detailCard {
  width: 600px;
  height: 400px;
  border: 1px solid rgb(238, 238, 238);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.cardHeader {
  display: flex;
  height: 80px;
  justify-content: space-between;
  padding: 0px 24px;
  align-items: center;
}

h1 {
  display: block;
  font-size: 2em;
  -webkit-margin-before: 0.67em;
          margin-block-start: 0.67em;
  -webkit-margin-after: 0.67em;
          margin-block-end: 0.67em;
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;
  font-weight: bold;
}

.prevButton {
  border: 1px solid rgb(221, 221, 221);
  height: 40px;
  width: 120px;
  background-color: rgb(255, 255, 255);
  border-radius: 12px;
  cursor: pointer;
}

.cardTitle {
  padding: 0px 24px;
}

.cardDesc {
  padding: 0px 24px;
}

